.header {
  position: sticky;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 70px;
  box-shadow: 0 1px 6px 0 $color-secondary;

  &__border-bottom--active {
    box-shadow: 0 5px 6px 0 $color-accent;
  }
}

.fade-slide-out {
  animation: fadeSlideOut 3s forwards;
}

@keyframes fadeSlideOut {
  0% {
    transform: translateX(0);
    opacity: 1;
  }
  100% {
    transform: translateY(-100%);
    box-shadow: none;
    opacity: 0;
  }
}
